/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import { jsx } from 'theme-ui';
import styled from '@emotion/styled/macro';
import { Link as GatsbyLink } from 'gatsby';
import { Flex, Box, Text } from 'rebass';
import { Button, Card, Container } from 'react-bootstrap';
import strings from '@gatsbystorefront/gatsby-theme-storefront-shopify/src/templates/main/strings';

const {ariaPromoButtonLabel} = strings;

const Image = styled(GatsbyImage)``;

const MainPageBanner = (props) => {
  const { banner, data } = props;

  return (
    <Flex sx={{ position: 'relative', overflow: 'hidden' }} >
      <Box position={'absolute'}>
        <img src={banner.src} alt="" />
      </Box>
      <Box width={[1, 4 / 5, 2 / 3]} mx={'auto'} pt={[145, 100]} pb={10} pl={[20, 24, 50]} pr={[0, '10vw']} color={'#fff'} textAlign={['center', 'left', 'left']}>
        <img sx={{ maxWidth: '130px' }} src={banner.logo} alt='sponsor logo' />
        <Text mb={["15px", "15px", "8px"]} mt={[20, 0]} fontSize={[24, 36, 60]} sx={{ textShadow: 'text-shadow: 0 0 4px rgba(0,0,0,0.4)' }} lineHeight={[1.5, 1.33, 1.33]}>{banner.header}</Text>
        <Text mb={[24, 40]}>{banner.subheader}</Text>
        <Button className="btn-dark btn-xlg rounded-0 font-weight-light text-uppercase">
          <GatsbyLink to={banner.link}>{ariaPromoButtonLabel}</GatsbyLink>
        </Button>
        <Text>{banner.note}</Text>
      </Box>
    </Flex>
  );
};

export default MainPageBanner;
