/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import { jsx } from 'theme-ui';
import { Link as GatsbyLink } from 'gatsby';
import { Flex, Box, Button, Text } from 'rebass';
import strings from './strings.json';
import styled from '@emotion/styled/macro';

const { ariaGiftSetsButtonLabel } = strings;

const StyledBox = styled(Box)`
  & img {
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const MainPageTextImageBlock = (props) => {
  const { textImage } = props;

  return (
    <Box bg={'background'} px={[20, 24, 50]} py={50} position={"relative"}>
      <Flex sx={{ flexDirection: ['column', 'row', 'row'] }} textAlign={['center', 'left', 'left']}>
        <Box width={[1, 1 / 2]}>
           <img sx={{ width: '100%', position: 'relative' }} src={textImage.src} alt={textImage.handle} />
        </Box>
        <Box width={[1, 1 / 2]}>
          <Text mb={17} fontSize={['8vw', '4vw']} lineHeight={'1.5'} sx={{ textTransform: 'capitalize' }}>{textImage.header}</Text>
          <Text mb={26} fontSize={['4vw', '1.5vw']} lineHeight={'1.43'} width={['80vw', '40vw', '35vw']} marginX={['auto', 0]}>{textImage.subheader}</Text>
          <Button padding={'1.4vw 3vw'} className="btn-dark rounded-0 text-uppercase font-weight-light" sx={{ margin: ['auto', 'unset' ], width: 'fit-content', fontSize: '1vw', fontWeight: '500', letterSpacing: '.2vw' }}>
            <GatsbyLink to={textImage.link}>{ ariaGiftSetsButtonLabel }</GatsbyLink>
          </Button>
        </Box>
      </Flex>
    </Box>
  );
};

export default MainPageTextImageBlock;
