/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import { jsx } from 'theme-ui';
import { Link as GatsbyLink } from 'gatsby';
import { Flex, Box, Button, Text } from 'rebass';
import strings from './strings.json';
import styled from '@emotion/styled/macro';

const { ariaGiftSetsButtonLabel } = strings;

const StyledBox = styled(Box)`
  & img {
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }

  &:hover img {
    transform: scale(1.05);
    transition-property: all;
    transition-duration: 0.6s;
    transition-timing-function: ease-in-out;
    transition-delay: 0s;
  }
`;

const MainPageHeader = (props) => {
  const { header, data } = props;

  return (
    <Box bg={'background'} position={"relative"} m={"auto"} p={['50px 20px', '50px 24px', '100px 50px']}>
      <Box width={1} sx={{ position: 'relative' }}>
        <Box sx={{ content: '""', background: 'url(https://cdn.shopify.com/s/files/1/0117/9005/7534/files/round-border.png) center/contain no-repeat',  height: ['150px', '150px', '212px'], width: ['150px', '150px', '212px'], position: ['relative', 'absolute', 'absolute'], transform: ['translate(-50%, 0)', 'translate(-50%, -50%)', 'translate(-50%, -50%)'], top: [0, '-10vw', '-10vw'], left: ['50%', 'unset'], right: ['unset', '-5vw'], zIndex: '2' }}>
          <Text sx={{ fontSize: [ 12, 12, 2], fontWeight: '500', textAlign: 'center', lineHeight: ['1.33', '1.33', '1.5'], color: '#00662E', position: 'relative', top: '50%', transform: 'translateY(-50%)' }}>
            N1<br/>
            Shea Butter<br/>
            Cosmetic Brand
          </Text>
        </Box>
      </Box>
      <Box className="card border-0 mx-auto" width={[null, '29rem', '36rem']} textAlign={'center'}>
        <Text className="card-title mx-auto my-4" fontSize={[3, 35, 6]} fontWeight={'body'} lineHeight={'heading'} >{header.header}</Text>
        <Text className="card-text" fontSize={[2, 2, 3]} fontWeight={'light'} lineHeight={'body'} px={[0, '15%']}>{header.subheader}</Text>
      </Box>
    </Box>
  );
};

export default MainPageHeader;
