/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React from 'react';
import { jsx } from 'theme-ui';
import { Flex, Box, Heading, Text } from 'rebass';
import { Link as GatsbyLink } from 'gatsby';
import { Button } from 'react-bootstrap';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  DotGroup,
} from 'pure-react-carousel';
import 'pure-react-carousel/dist/react-carousel.es.css';
import './carousel.css';

import ChevronLeft from '../../components/Icons/ChevronLeft';
import ChevronRight from '../../components/Icons/ChevronRight';
import strings from './strings.json';

const { ariaNextButtonLabel, ariaBackButtonLabel, ariaShopButtonLabel } = strings;

const MainPageCarousel = (props) => {
  const { carousel, data, gatsbyImageProps } = props;
  return (
    <Box width={1} sx={{ position: 'relative' }}>
      <CarouselProvider
        naturalSlideWidth={1300}
        naturalSlideHeight={800}
        totalSlides={carousel.children.length}
        isPlaying
        infinite
      >
        <Slider>
          {carousel.children.map((slide, index) => {
            return (
              <Slide key={index} index={index}>
                <Box position={'absolute'}>
                  <img src={slide.bg} alt={slide.name} />
                </Box>
                <Flex>
                  <Box className="card-body pl-md-3 pl-lg-5" >
                    <Heading className="card-title" mb={['1rem', null, '1.2rem']} fontFamily={'sans-serif'} fontWeight={'light'} fontSize={['29px', '4vw']} lineHeight={'1.25'}>{slide.header}</Heading>
                    <Text className="card-text pr-lg-5" mb={['1rem', null, '1.2rem']} fontSize={['5vw', '2vw', '1.2vw']}>{slide.subheader}</Text>
                    <Button className="btn-dark btn-xlg rounded-0 font-weight-light text-uppercase">
                      <GatsbyLink to={slide.link} color={'white'} >{ariaShopButtonLabel}</GatsbyLink>
                    </Button>
                  </Box>
                  <Box className="card border-0 order-sm-2 m-auto" sx={{ background: 'transparent', order: ['1', '2'] }} >
                    <img src={slide.src} sx={{ width: '100%', padding: ['3rem', '0 2vw', '0 6vw'] }} />
                  </Box>
                </Flex>
              </Slide>
            );
          })}
        </Slider>

        <ButtonBack
          aria-label={ariaBackButtonLabel}
          sx={{
            display: ['none', 'block'],
            position: 'absolute',
            bottom: ['36%', '50%'],
            left: 1,
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: [0, 3],
          }}
        >
          <ChevronLeft width="40px" height="40px" />
        </ButtonBack>

        <ButtonNext
          aria-label={ariaNextButtonLabel}
          sx={{
            display: ['none', 'block'],
            position: 'absolute',
            bottom: ['36%', '50%'],
            right: 1,
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: 2,
            px: [0, 3],
          }}
        >
          <ChevronRight width="40px" height="40px" />
        </ButtonNext>

        <DotGroup
          sx={{
            display: ['none', 'block'],
            position: 'absolute',
            bottom: '10%',
            left: ['40%', '45%'],
            'z-index': '2',
            border: 0,
            bg: 'transparent',
            color: 'primary',
            py: '2',
            px: '3',
          }}
        />
      </CarouselProvider>
    </Box>
  );
};

export default MainPageCarousel;
